import React, { useState, useRef } from 'react';

function Search() {
  

  const inpRef = useRef();
  const txt = React.useRef(0);
  let [fieldTxt, changeTxt] = useState()
  let [iter, add] = useState(0)
  let [swd, swdSet] =useState()
  const [date, setDate] = useState([])
  let currentDate = '';
let i=0;
  function handleQuery(){
    const getmaindiv = document.getElementById("mainYear");
    getmaindiv.classList.remove("slideDown");

    setTimeout(function(){
      document.querySelectorAll("#mainYear")[0].style.visibility = 'visible'; 

      if(date.length>0){
        const firststored = document.querySelectorAll("#secondaryYear:nth-of-type(2)"); 
        //returns a node list
        firststored[0].style.visibility = "visible"
        //hiding first element cause it's equal to search result
      }
    const currentQuery = inpRef.current.value;
    changeTxt(fieldTxt = 'Loading...')
        fetch(`https://numbersapi.p.rapidapi.com/${currentQuery}/year?json`, {
          "method": "GET",
          "headers": {
              "x-rapidapi-host": "numbersapi.p.rapidapi.com",
              "x-rapidapi-key": "e9b9061f49msh537cfe3ef159410p12ee92jsn50bb66033aae"
          }
      })
        .then(data => data.json())
        .then(data => {
            txt.current = data.text;
            changeTxt(fieldTxt = txt.current)
            add(iter = iter + 1)
            currentDate = fieldTxt;
            getmaindiv.classList.add("slideDown");

            setDate(pdates=> [<div key={currentDate} id="secondaryYear"><center><i>{currentDate}</i></center></div>, ...pdates])
            inpRef.current.value = '';
            //adding previous to history and clearing input
        })
        .catch(err=> {
          changeTxt(fieldTxt = 'Error... Try again')
          console.log(err)
          inpRef.current.value = '';
        })
    },20);
    
 
  }

  function isChange(){
    const q = inpRef.current.value;
    window.setTimeout(function(){
      if(q == inpRef.current.value){
        handleQuery();
      }
    },750)
  }

  return (
    <>
    <center>
    <div className="menu">
    <div id="bigbimg"><img id ="bbico" src={ require('./bbico.png') } /></div>
    <div>
    <div id="title">YEAR SEARCH ENGINE</div><br></br>
    <input type="text" onChange={isChange} ref={inpRef} placeholder="enter the year..."></input>
    </div>
    </div>
    </center>
    
    <div id="mainYear"><center><i>{fieldTxt}</i></center></div>
    <br></br>
    {date}
    </>
  );
  

}


export default Search;